<template>
  <div id="userRegisterView">
    <h2 style="margin-bottom: 16px"></h2>
    <a-form
      style="max-width: 480px; margin: 0 auto"
      label-align="left"
      auto-label-width
      :model="form"
      @submit="handleSubmit"
    >
      <a-form-item field="userAccount" label="">
        <a-input v-model="form.userAccount" placeholder="" />
      </a-form-item>
      <a-form-item field="userPassword" tooltip=" 8 " label="">
        <a-input-password
          v-model="form.userPassword"
          placeholder=""
        />
      </a-form-item>
      <a-form-item
        field="userPassword"
        tooltip=" 8 "
        label=""
      >
        <a-input-password
          v-model="form.checkPassword"
          placeholder=""
        />
      </a-form-item>
      <a-form-item>
        <a-button
          type="primary"
          html-type="submit"
          style="width: 120px; margin-right: 20px"
        >
          
        </a-button>
        <a-checkbox
          @change="handleRoleChange"
          @click="handleRoleClick"
          style="margin-right: 0%; text-align: left; width: 300px"
          :disabled="store.state.user.loginUser?.userRole !== 'admin'"
        >
          
        </a-checkbox>

        <a-button
          type="text"
          style="width: 290px; text-align: center"
          @click="handleLogin"
        >
          
        </a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script setup lang="ts">
import { reactive, ref } from "vue";
import message from "@arco-design/web-vue/es/message";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import { UserControllerService, UserLoginRequest } from "../../../generated";
/**
 * 
 */
const form = reactive({
  userAccount: "",
  userPassword: "",
  checkPassword: "",
  userRole: "",
});

const router = useRouter();
const store = useStore();

/**
 * 
 * @param data
 */
const handleSubmit = async () => {
  if (form.userPassword.length < 8) {
    message.error("8");
    return;
  }
  if (form.userPassword !== form.checkPassword) {
    message.error("");
    return;
  }
  const res = await UserControllerService.userRegisterUsingPost(form);
  if (res.code === 0) {
    router.push({
      path: "/user/login",
      replace: true,
    });
  } else {
    message.error("" + res.message);
  }
};
const handleLogin = () => {
  router.push("/user/login");
};
const handleRoleChange = (e: any) => {
  form.userRole = e ? "admin" : "user";
  console.log(form.userRole);
};
const handleRoleClick = (e: any) => {
  if (store.state.user.loginUser?.userRole !== "admin") {
    e.preventDefault();
    message.error("");
  }
};
</script>
