<template>
  <div id="userLoginView">
    <h2 style="margin-bottom: 16px"></h2>
    <a-form
      style="max-width: 480px; margin: 0 auto"
      label-align="left"
      auto-label-width
      :model="form"
      @submit="handleSubmit"
    >
      <a-form-item field="userAccount" label="">
        <a-input v-model="form.userAccount" placeholder="" />
      </a-form-item>
      <a-form-item field="userPassword" tooltip=" 8 " label="">
        <a-input-password
          v-model="form.userPassword"
          placeholder=""
        />
      </a-form-item>
      <a-form-item>
        <a-button
          type="primary"
          html-type="submit"
          style="width: 120px; margin-right: 100px"
        >
          
        </a-button>
        <a-button
          type="text"
          style="width: 290px; text-align: center"
          @click="handleRegister"
        >
          
        </a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script setup lang="ts">
import { reactive } from "vue";
import message from "@arco-design/web-vue/es/message";
import { UserControllerService, UserLoginRequest } from "../../../generated";
import { useRouter } from "vue-router";
import { useStore } from "vuex";

/**
 * 
 */
const form = reactive({
  userAccount: "",
  userPassword: "",
});

const router = useRouter();
const store = useStore();

/**
 * 
 * @param data
 */
import { useRoute } from "vue-router";

const route = useRoute();
const handleSubmit = async () => {
  const res = await UserControllerService.userLoginUsingPost(form);
  // 
  if (res.code === 0) {
    await store.dispatch("user/getLoginUser");
    const redirectPath = route.query.redirect || "/";
    router.push({ path: redirectPath as string, replace: true });
  } else {
    message.error("" + res.message);
  }
};
const handleRegister = () => {
  router.push({
    path: "/user/register",
    replace: false,
  });
  console.log("");
};
</script>
