<template>
  <div id="QuestionAddView">
    <h2 style="text-align: left"></h2>
    <a-form :model="form" style="width: 100%" @submit="handleSubmit">
      <a-form-item
        field="title"
        tooltip=""
        label=""
        label-align="left"
        label-col-flex="100px"
      >
        <a-input
          v-model="form.title"
          placeholder="please enter your title"
          style="width: 100%"
        />
      </a-form-item>

      <a-form-item
        field="tags"
        tooltip=""
        label=""
        style="width: 100%"
        label-align="left"
        label-col-flex="100px"
      >
        <div style="width: 100%">
          <tags-input
            :tagsChose="tagsChose"
            :handChange="handChange"
            :handDelete="handDelete"
            style="width: 100%"
          />
        </div>
      </a-form-item>

      <a-form-item
        field="content"
        tooltip=""
        label=""
        label-col-flex="100px"
        style="width: 100%"
      >
        <md-editor
          :value="form.content"
          @change="onContentChange"
          style="width: 100%"
        />
      </a-form-item>

      <a-form-item
        field="answer"
        tooltip=""
        label=""
        label-col-flex="100px"
      >
        <md-editor
          :value="form.answer"
          @change="onAnswerChange"
          style="width: 100%"
        />
      </a-form-item>

      <a-form-item
        label=""
        :content-flex="false"
        :merge-props="false"
        label-col-flex="100px"
      >
        <a-space direction="vertical" style="min-width: 480px; width: 100%">
        </a-space>
      </a-form-item>
      <a-form-item
        field="judgeConfig.timeLimit"
        label=""
        label-col-flex="100px"
      >
        <a-input-number
          v-model="form.judgeConfig.timeLimit"
          placeholder=""
          mode="button"
          min="0"
          size="large"
          default-value="1000"
          style="width: 100%"
        />
      </a-form-item>

      <a-form-item
        field="judgeConfig.memoryLimit"
        label=""
        label-col-flex="100px"
      >
        <a-input-number
          v-model="form.judgeConfig.memoryLimit"
          placeholder=""
          mode="button"
          min="0"
          size="large"
          default-value="256"
          style="width: 100%"
        />
      </a-form-item>

      <a-form-item
        field="judgeConfig.stackLimit"
        label=""
        label-col-flex="100px"
      >
        <a-input-number
          v-model="form.judgeConfig.stackLimit"
          placeholder=""
          mode="button"
          min="0"
          size="large"
          default-value="256"
          style="width: 100%"
        />
      </a-form-item>

      <a-form-item
        label=""
        :content-flex="false"
        :merge-props="false"
        label-col-flex="100px"
      >
        <a-button @click="handleAdd" type="outline" status="success"
          ></a-button
        >
      </a-form-item>
      <a-form-item no-style>
        <a-row
          :gutter="24"
          v-for="groupIndex in Math.ceil(form.judgeCase.length / 2)"
          :key="groupIndex"
          style="width: 100%; margin-bottom: 16px"
        >
          <!--  -->
          <a-col :span="12" v-if="form.judgeCase[(groupIndex - 1) * 2]">
            <a-card :bordered="true">
              <a-row :gutter="16">
                <!--  -->
                <a-col :span="20">
                  <a-form-item
                    :field="`form.judgeCase[${(groupIndex - 1) * 2}].input`"
                    label=""
                    label-col-flex="100px"
                  >
                    <a-input
                      v-model="form.judgeCase[(groupIndex - 1) * 2].input"
                      placeholder=""
                    />
                  </a-form-item>
                  <a-form-item
                    :field="`form.judgeCase[${(groupIndex - 1) * 2}].output`"
                    label=""
                    label-col-flex="100px"
                  >
                    <a-input
                      v-model="form.judgeCase[(groupIndex - 1) * 2].output"
                      placeholder=""
                    />
                  </a-form-item>
                </a-col>
                <!--  -  -->
                <a-col :span="4" style="display: flex; align-items: center">
                  <a-button
                    status="danger"
                    @click="handleDelete((groupIndex - 1) * 2)"
                    style="height: fit-content"
                  >
                    
                  </a-button>
                </a-col>
              </a-row>
            </a-card>
          </a-col>

          <!--  -->
          <a-col :span="12" v-if="form.judgeCase[(groupIndex - 1) * 2 + 1]">
            <a-card :bordered="true">
              <a-row :gutter="16">
                <!--  -->
                <a-col :span="20">
                  <a-form-item
                    :field="`form.judgeCase[${(groupIndex - 1) * 2 + 1}].input`"
                    label=""
                    label-col-flex="100px"
                  >
                    <a-input
                      v-model="form.judgeCase[(groupIndex - 1) * 2 + 1].input"
                      placeholder=""
                    />
                  </a-form-item>
                  <a-form-item
                    :field="`form.judgeCase[${
                      (groupIndex - 1) * 2 + 1
                    }].output`"
                    label=""
                    label-col-flex="100px"
                  >
                    <a-input
                      v-model="form.judgeCase[(groupIndex - 1) * 2 + 1].output"
                      placeholder=""
                    />
                  </a-form-item>
                </a-col>
                <!--  -  -->
                <a-col :span="4" style="display: flex; align-items: center">
                  <a-button
                    status="danger"
                    @click="handleDelete((groupIndex - 1) * 2 + 1)"
                    style="height: fit-content"
                  >
                    
                  </a-button>
                </a-col>
              </a-row>
            </a-card>
          </a-col>
        </a-row>
      </a-form-item>
      <a-form-item>
        <a-button type="primary" style="min-width: 200px" @click="doSubmit"
          >
        </a-button>
      </a-form-item>
    </a-form>
  </div>
</template>
<script setup lang="ts">
import { ref, watch } from "vue";
import message from "@arco-design/web-vue/es/message";
import { QuestionControllerService } from "../../../generated";
const form = ref({
  title: "",
  tags: [],
  answer: "",
  content: "",
  judgeConfig: {
    memoryLimit: 1000,
    stackLimit: 1000,
    timeLimit: 1000,
  },
  judgeCase: [
    {
      input: "",
      output: "",
    },
  ],
});
const doSubmit = async () => {
  const res = await QuestionControllerService.addQuestionUsingPost(form.value);
  if (res.code == 0) {
    message.success("");
  } else {
    message.error("");
  }
};
/**
 * * @description 
 */
import TagsInput from "@/components/TagsInput.vue";
import MdEditor from "@/components/MdEditor.vue";
const tagsChose = ref([]);
const handChange = (
  tag: { value: string; checked: boolean },
  checked: boolean
) => {
  if (checked) {
    tagsChose.value.push(tag);
  } else {
    tagsChose.value = tagsChose.value.filter(
      (item) => item.value !== tag.value
    );
  }
};
const handDelete = (tag: { value: string; checked: boolean }) => {
  tagsChose.value = tagsChose.value.filter((item) => item.value !== tag.value);
  tag.checked = false;
  console.log("tag", tag);
  console.log("tagsChose", tagsChose.value);
};
watch(
  () => tagsChose,
  (newTags) => {
    form.value.tags = newTags._rawValue.map((item) => item.value);
  },
  { deep: true }
);
/**
 * * @description 
 */
const onContentChange = (value: string) => {
  form.value.content = value;
};
/**
 * * * @description 
 */
const onAnswerChange = (value: string) => {
  form.value.answer = value;
};
/**
 * 
 */
const handleAdd = () => {
  form.value.judgeCase.push({
    input: "",
    output: "",
  });
};

/**
 * 
 */
const handleDelete = (index: number) => {
  form.value.judgeCase.splice(index, 1);
};
</script>
